video {
  width: 1000px;
  height: 100vh;
  margin: auto;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.paragraph-underline {
  padding-bottom: 5px;
  border-bottom: 2px solid grey;
  width: 8%;
}

.paragraph-text {
  font-size: 16px;
  width: 70%;
  line-height: 25px;
  margin-bottom: 20px;
}

.details-header {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding-top: 40px;
}

.item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 40px;
  padding-bottom: 20px;
  img {
    width: 400px;
    height: 250px;
    border-radius: 4px;
  }

  div {
    align-self: center;
    p {
      font-size: 16px;
    }
  }
}
